<template>
  <div class="search-result">
    <nav-bar class="home-nav">
      <div slot="left" class="left" @click="$router.go(-1)">
        <img src="~assets/img/common/back.svg" alt />
      </div>
      <div slot="center" class="header">
        <img src="@/styles/img/4、微网站搜索_03.png" alt />
      </div>
    </nav-bar>
    <div class="search">
      <div class="search-box">
        <input type="text" placeholder="搜索店内精品" v-model="value" autocomplete="off" />
        <span @click="handleSearch">搜索</span>
        <i class="iconfont icon-sousuo"></i>
      </div>
    </div>
    <section>
      <goods-list :goods="goods" title="搜索结果"></goods-list>
    </section>
  </div>
</template>

<script>
import NavBar from "components/common/navbar/NavBar";
import GoodsList from "components/common/goodsList/GoodsList";
export default {
  name: "SearchResult",
  data() {
    return {
      value: "",
      page: 1,
      pageSize: 5,
      goods: [],
      history: []
    };
  },
  components: {
    NavBar,
    GoodsList
  },
  created() {},
  mounted() {
    // 接收链接的值
    let value = this.$route.query.value;
    this.value = value;
    this.fetchGoodsData(value);
    // 设置history
    if (localStorage.getItem("history")) {
      this.history = JSON.parse(localStorage.getItem("history"));
    }
  },
  beforeRouteLeave(to, from, next) {
    localStorage.setItem("history", JSON.stringify(this.history));
    next();
  },
  methods: {
    async fetchGoodsData(name) {
      let res = await this.$http.get(this.$api.GOODS, {
        state:1,
        page: this.page,
        pageSize: this.pageSize,
        name
      });
      console.log(res);
      this.goods = res.data.data;
    },
    handleSearch() {
      if (this.value == "") {
        this.$toast("请输入搜索的内容");
      } else if (this.value.length > 6) {
        this.$toast("搜索内容不宜过长");
      } else {
        let index = this.history.indexOf(this.value);
        if (index !== -1) {
          this.history.splice(index, 1);
        }
        this.history.unshift(this.value);

        if (this.history.length > 9) {
          this.history = this.history.slice(0, 9);
        }
        this.fetchGoodsData(this.value);
      }
    }
  }
};
</script>
<style lang='scss' scoped>
@import "styles/search";
.header img {
  width: 1.94rem;
  height: 0.4rem;
  margin-top: 0.24rem;
}
.left {
  padding-top: 5px;
  padding-left: 5px;
}
section {
  width: 100%;
  height: auto;
  background-color: #f6f6f6;
  margin-top: 0.26rem;
  margin-bottom: 0.98rem;
}

.search {
  width: 100%;
  height: 0.78rem;
  padding: 0 0.32rem;
  background-color: #fff;
  box-sizing: border-box;
  position: relative;
}
.search input {
  width: 5.5rem;
  height: 0.6rem;
  margin-top: 0.06rem;
  box-sizing: border-box;
  padding-left: 0.65rem;
  float: left;
  outline: none;
  border-radius: 0.3rem;
  border: solid 0.01rem #d9d9d9;
  opacity: 0.6;
}
</style>
